@charset "utf-8";
body {
	margin: 0;
	padding: 0;
	background-color: #fafafa;
	font-family: '맑은고딕';
}

.todo_list_template {
	width: 94%;
	margin: 4rem auto 0 auto;
	background-color: #fff;
	box-shadow: 0px 3px 6px #ccc;
}
.title {
	padding: 2rem;
	font-size: 2.5rem;
	text-align: center;
	font-weight: bold;
	background: rgb(0, 140, 255);
	color: #fff;
}
.form_wrapper {
	padding: 1rem;
	border-bottom: 1px solid rgb(0, 140, 255);
}
.todos_wrapper {
	min-height: 5rem;
}
