@charset "utf-8";

.form {
	display: flex;
}
.form input {
	flex-grow: 1;
	/* 버튼의 가로너비를 제외한 나머지 영역 차지하기 */
	font-size: 1.25rem;
	outline: none;
	border: none;
	border-bottom: 1px solid rgb(0, 140, 255);
}
.add_list {
	padding: 0.85rem 1.2rem;
	margin-left: 1rem;
	border-radius: 3px;
	color: #fff;
	font-weight: bold;
	border: none;
	font-size: 0.9rem;
	background-color: rgb(0, 140, 255);
}

.add_list:hover {
	background: rgb(43, 158, 253);
}
