@charset "utf-8";

.todo_item {
	display: flex;
	cursor: pointer;
	border-bottom: 1px solid #f3f3f3;
	line-height: 50px;
	height: 50px;
	width: 100%;
}
.todo_item:last-child {
	border-bottom: none;
}
.todo_item:hover {
	background-color: #ececec;
}
.todo_item:hover .remove {
	opacity: 1;
}

.remove {
	opacity: 0;
	order: 2;
	font-weight: bold;
	color: #787878;
	margin-left: 0.5rem;
	padding-right: 20px;
	font-size: 1.5rem;
	line-height: 45px;
}

.todo_text {
	flex-grow: 1;
	order: 1;
	line-height: 50px;
}

.checked {
	text-decoration: line-through;
	color: #787878;
}
.todo_check span:first-child {
	padding-left: 20px;
}
.check_mark {
	font-size: 1.5rem;
	line-height: 1rem;
	color: #f00;
	font-weight: bold;
	margin-left: 5px;
}
